import styled from "styled-components";

const primaryColor = "rgb(250,114,104)";

const Link = styled.a`
  background: rgba(0, 0, 0, 0);
  color: ${primaryColor};

  font-size: 1em;
  font-family: "Noto Sans KR", sans-serif;
  font-weight: 500;
  padding: 0.25em 1em;
  border-radius: 3px;
  cursor: pointer;
  outline: none;
  text-decoration: none;
  transition: ease 0.2s;

  &:hover {
    background: rgba(250, 114, 104, 0.1);
  }
`;

export default Link;
