import styled from "styled-components";

const Input = styled.input`
  background: white;
  border: 2px solid #eee;
  border-radius: 3px;
  outline: none;
  font-size: 1em;
  font-family: "Noto Sans KR", sans-serif;
  color: #333;
  padding: 3px 5px;
  transition: ease 0.2s;

  ::placeholder {
    color: #666;
  }

  &:focus {
    box-shadow: 0 0 0 3pt rgba(104, 167, 250, 0.5);
  }
`;

export default Input;
